<template>
	<div class="credit-manage-card-set">
		<div class="cell-part">
			<mt-cell title="" :value="detail.ACCOUNT"></mt-cell>
		</div>
		<div class="cell-part">
			<mt-cell @click.native="toSet('CreditManageCardSetRename')" title="" :value="detail.ALIAS || ''" is-link></mt-cell>
		</div>
		<div class="cell-part">
			<mt-cell @click.native="toSet('CreditManageCardSetCost')" title="" :value="detail.PINCHK" is-link></mt-cell>
		</div>
		<div class="cell-part">
			<mt-cell @click.native="toSet('QuickLoss')" title="" is-link></mt-cell>
			<mt-cell @click.native="toSet('PatchCard')" title="" is-link></mt-cell>
		</div>
		<div class="delete-button">
			<a @click="submit" href="javascript:;"></a>
		</div>
	</div>
</template>

<script>

import service from '@/services/my/my-bank-card';

export default {
	data() {
		return {
			detail: {}
		};
	},
	created() {
		let detail = this.$route.params.detail;
		if(detail){
			this.$ls.setItem('creditMsg', this.$route.params.detail);
		}
		this.detail = this.$ls.getItem('creditMsg');
	},
	methods: {
		toSet(name) {
			this.$router.push({name: name});
		},
		submit() {
			let account = this.detail.ACCOUNT.substring((this.detail.ACCOUNT.length - 4), this.detail.ACCOUNT.length);
			this.$confirm(`${account}${this.detail.ACCT_JGMC}`, {
                yes: { text: '' },
                no: { text: '' }
            }).then((result)=>{
				this.deleteCredit();
            }, (result)=>{
                console.log(result);
            });
		},
		deleteCredit() {
			service.deleteCredit({
				ID: '',
				ACCT_TYPE: '3',
				PAYER_ACCOUNT: this.detail.ACCOUNT,
				OPT_TYPE: '2'
			}).then( data => {
				this.$router.push({name: 'CreditManageCardSetSuccess', params: {type: 'delete'}});
			}, err => {
				console.log(err);
			});
		}

	}
};

</script>

<style lang="scss">
	
.credit-manage-card-set{
	.delete-button{
		margin-top: 80px;
		padding: 24px 0;
		text-align: center;
		background-color: #fff;
		a{
			color: #FF511A;
			font-size: 15px;
		}
	}
}

</style>


