import ajax from '@/services/ajax';
import api from '@/services/api';

/*   */
export function queryCreditList(data) {
    return ajax.postAfterLoginQueryList('012022', data, api.queryCreditList);
}

/*    */
export function queryCurrentBill(data) {
    return ajax.postAfterLoginQueryList('015002', data, api.queryCurrentBill);
}

/*    */
export function queryOutBill(data) {
    return ajax.postAfterLoginQueryList('015003', data, api.queryOutBill);
}

